.project_detais{
    padding: 2em 0;
}

.project_detais h1,
.project_detais h2,
.project_detais p{
    margin-bottom: .5em;
}

.project_detais h1{
    background-color: #222;
    color: #ffbb33;
    padding: .4em;
}

.project_detais span{
    font-weight: bold;
}

.details_container,
.service_form_container{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid #7a7a7a;
    margin-bottom: 1.2em;
    padding-bottom: 1.2em;
}

.btn{
    width: 150px;
    background-color: #222;
    color: #FFF;
    padding: .5em 1em;
    text-decoration: none;
    transition: .5s;
    cursor: pointer;
    max-height: 40px;
    border: none;
}

.btn:hover{
    color: #FFBB33;
}

.project_info{
    width: 100%;
}