.footer{
    padding: 3em;
    background-color: #222;
    color: #FFF;
    text-align: center;
}

.social_list{
    display: flex;
    justify-content: center;
    list-style-type: none;
}

.social_list li{
    margin: 0 1em;
}

.social_list li:hover{
    color: #FFBB33;
}

.social_list li a{
  text-decoration: none;
  color: inherit;
}

.social_list li svg{
    font-size: 1.5em;
    cursor: pointer;
}

.copy_right{
    margin-top: 2em;
}

.copy_right span{
    font-weight: bold;
    color: #FFBB00;
}