.projectCard{
    width: 24%;
    padding: 1em;
    margin: .5%;
    border: 1px solid #7a7a7a;
    border-radius: 5px;
}

.projectCard h4{
    background-color: #222;
    color: #ffbb33;
    padding: .4em;
    margin-bottom: 1.3em;
    font-size: 1.3em;
}

.projectCard p{
    color: #7a7a7a;
    margin-bottom: 1em;
}

.projectCard p span{
    font-weight: bold;
}

.category_text{
    display: flex;
    align-items: center;
}

.category_text span{
    display: block;
    width: 12px;
    aspect-ratio: 1/1;
    border-radius: 50%;
    background-color: #ccc;
    margin-right: 5px;
}

.category_text .infra{
    background-color: #ffaebc;
}

.category_text .desenvolvimento{
    background-color: #a0e7e5;
}

.category_text .design{
    background-color: #84f8c8;
}

.category_text .planejamento{
    background-color: #fbe7c6;
}

.projectCard_actions{
    margin-top: 1.2em;
    display: flex;
    align-items: center;
}

.projectCard_actions a,
.projectCard_actions button{
    text-decoration: none;
    border: none;
    background-color: #fff;
    color: #222;
    font-size: .9em;
    padding: .6em 1em;
    margin-right: 1em;
    cursor: pointer;
    border: 1px solid #222;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .5s;
}

.projectCard_actions svg{
    margin-right: .5em;
}

.projectCard_actions a:hover,
.projectCard_actions button:hover{
    background-color: #222;
    color: #ffbb33;
}